.App {
  width: 100%;
  text-align: center;
}

.controls {
  width: 50rem;
  display: flex;
  justify-content: space-around;
  font-size: 2.5rem;
}

#header {
  text-align: center;
  width: 100%;
}

#sessionDiv {
  width: 15rem;
  height: 10rem;
  margin: 35px;
  padding: 3px;
  font-size: 3rem;
  border: 2.5px solid black;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#working {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
}

#buttons {
  font-size: 2rem;
  margin-top: -2rem;
  width: 5rem;
}

p {
  margin: 2px;
}

h1 {
  font-size: 3rem;
}
button {
  background-color: #1e555c;
  border: none;
  font-size: 1.7rem;
  color: white;
  outline: none !important;
}
.arrow {
  margin: 5px;
}

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 3.5rem;
  }
  #header {
    width: 200%;
    margin-left: 20px;
    text-align: center;
  }
  #working {
    margin-left: 25rem;
  }
  .controls {
    flex-direction: column;
    font-size: 3rem;
  }
  .customizeDiv {
    margin: 2rem;
  }
  .button {
    font-size: 2.5rem;
  }
  #buttons {
    width: 8rem;
  }
}